import FooterBar from "../components/footer/page";
import Header from "../components/header/page";

export default function MainLayout({ children }) {
  return (
    <>
      <div
        style={{
          height: "100vh",
          display: "flex",
          flexDirection: "column",
          overflow: "hidden",
        }}
      >
        {/* Sticky Header */}
        <Header />

        {/* Scrollable Content Area */}
        <div
          className="main-layout  no-scrollbar"
          style={{
            flex: 1,
            overflowY: "auto",
            paddingTop: "70px", // height of the header
            paddingBottom: "60px", // height of the footer
          }}
          ca
        >
          <div className="page-content">
            {/* <Sidebar /> */}
            <main className="content">{children}</main>
          </div>
        </div>

        {/* Sticky Footer */}
        <FooterBar />
      </div>
    </>
    // <>
    //   <Header />
    //   <div className="main-layout">
    //     <div className="page-content">
    //       {/* <Sidebar /> */}
    //
    //       <FooterBar />
    //     </div>
    //   </div>
    // </>
  );
}
